<style lang="scss" src="./style.scss" scoped></style>

<template>
  <div class="container">
    <div v-bind:class="{'box-item': true, 'box-empty': !classStatus, 'box-have': classStatus}">
      <div class="empty" v-if="!classStatus">
        <span>{{ tipo }}</span>
      </div>
      <div class="have" v-if="classStatus">
        <span class="title">{{ tipo }}</span>
        <span class="hour">{{ hour }}:{{ minute }}<span>
        <span class="date">{{ date }} - {{ month.fullMonth() }} - {{ year }}</span>
        <span class="day">{{ fullDate.weekDay() }}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'clock-box',
  props: ['tipo', 'passdata'],
  data () {
    return {
      hour: '00',
      minute: '00',
      date: '00',
      month: '',
      year: '0000',
      fullDate: '00:00:00',
      classStatus: false
    }
  },
  watch: {
    passdata: function (pData, pOldData) {
      this.classStatus = true
      this.hour = pData.hour
      this.minute = pData.minute
      this.date = pData.date
      this.month = pData.month
      this.year = pData.year
      this.fullDate = pData.fullDate
    }
  }
}

/* eslint no-extend-native: ["error", { "exceptions": ["Number"] }] */
Number.prototype.zerofill = function (vlr) {
  return ('0' + this.toString()).slice(-2)
}
</script>



