<style lang="scss" src="./style/app.scss"></style>

<template>
  <div id="app">
    <clock></clock>
    <receive v-on:savedlist="addList"></receive>
    <list :list="clockList"></list>
  </div>
</template>

<script>
import Clock from './components/clock/clock'
import Receive from './components/receive/receive'
import List from './components/list/list'
import ClockList from './fn-clock-list'

export default {
  name: 'app',
  components: {
    Clock,
    Receive,
    List
  },
  data () {
    return {
    }
  },
  methods: {
    addList: ($ev) => {
      // Convert Clock to ClockList type
      this.listContent = ClockList.convertClock2List($ev)
    }
  }
}
</script>

<style>
</style>



