<style lang="scss" src="./style.scss"></style>

<template>
  <div class="container">
    <div class="ctn">
      <clock-box class="clock-box" :tipo="'ENTRADA'" :passdata="item.workoutStart" v-droppable.clock="getWorkoutStart"></clock-box>
      <clock-box class="clock-box" :tipo="'INÍCIO ALMOÇO'" :passdata="item.launchStart" v-droppable.clock="getLaunchStart"></clock-box>
      <clock-box class="clock-box" :tipo="'FIM ALMOÇO'" :passdata="item.launchEnd" v-droppable.clock="getLaunchEnd"></clock-box>
      <clock-box class="clock-box" :tipo="'SAÍDA'" :passdata="item.workoutEnd" v-droppable.clock="getWorkoutEnd"></clock-box>
    </div>

    <div class="box-command">
      <button type="button" @click.prevent="saveList" name="button" v-bind:disabled="showSave">SALVAR</button>
    </div>
  </div>
</template>

<script>
import ClockBox from './clock-box/clock-box'

export default {
  name: 'receive',
  components: {
    ClockBox
  },
  data () {
    return {
      item: {
        workoutStart: null,
        workoutEnd: null,
        launchStart: null,
        launchEnd: null
      }
    }
  },
  computed: {
    showSave: function () {
      let ret = !this.item.workoutStart || !this.item.workoutEnd || !this.item.launchStart || !this.item.launchEnd
      return ret
    }
  },
  methods: {
    getWorkoutStart: function ($ev) {
      this.item.workoutStart = JSON.parse($ev)
    },
    getLaunchStart: function ($ev) {
      this.item.launchStart = JSON.parse($ev)
    },
    getLaunchEnd: function ($ev) {
      this.item.launchEnd = JSON.parse($ev)
    },
    getWorkoutEnd: function ($ev) {
      this.item.workoutEnd = JSON.parse($ev)
    },
    saveList: function () {
      this.$emit('savedlist', this.item)
      let nn = 10
      console.log(nn.fullMonth())
    }
  }
}

/* eslint no-extend-native: ["error", { "exceptions": ["Number", "String"] }] */
Number.prototype.fullMonth = function () {
  const monthLiteral = ['JANEIRO', 'FEVEREIRO', 'MARÇO', 'ABRIL', 'MAIO', 'JUNHO', 'JULHO', 'AGOSTO', 'SETEMBRO', 'OUTUBRO', 'NOVEMBRO', 'DEZEMBRO']
  // console.log(this)
  return monthLiteral[this]
}
String.prototype.weekDay = function () {
  const dayLiteral = ['DOMINGO', 'SEGUNDA-FEIRA', 'TERÇA-FEIRA', 'QUARTA-FEIRA', 'QUINTA-FEIRA', 'SEXTA-FEIRA', 'SABADO']
  return dayLiteral[new Date(this).getDay()]
}

</script>



